<!--  -->
<template>
  <div>
    <div class="header">
      <p>RGB</p>
    </div>
    <div class="main">
      <div class="main-one">
        <div class="box-one">
          <el-input v-model="inputOne" placeholder="请输入十六进制颜色" maxlength="6" clearable>
            <template slot="prepend">#</template>
          </el-input>
        </div>
        <div class="button" @click="changeOne">
          <el-button>转换</el-button>
        </div>
      </div>
      <div class="main-two" v-clipboard:copy="rgbcolor" v-clipboard:success="onCopy" v-clipboard:error="onError">
        <div class="two-one">{{rgb}}</div>
        <div class="two-two" :style="{background:rgbcolor}"></div>
      </div>
      <div class="main-three">
        <div class="box-one">
          <el-input
            v-model="r"
            onkeyup="this.value=this.value.replace(/[^0-9-]+/,'');"
            maxlength="3"
            clearable
          >
            <template slot="prepend">rgb</template>
          </el-input>
          <el-input
            v-model="g"
            onkeyup="this.value=this.value.replace(/[^0-9-]+/,'');"
            maxlength="3"
            clearable
          ></el-input>
          <el-input
            v-model="b"
            onkeyup="this.value=this.value.replace(/[^0-9-]+/,'');"
            maxlength="3"
            clearable
          ></el-input>
        </div>
        <div class="button" @click="changeTwo">
          <el-button>转换</el-button>
        </div>
      </div>
      <div class="main-two" v-clipboard:copy="hex" v-clipboard:success="onCopy" v-clipboard:error="onError">
        <div class="two-one">{{hex}}</div>
        <div class="two-two" :style="{background:hex}"></div>
      </div>
      <div class="main-four">
        <div class="box-one">
          <el-input v-model="all" placeholder="如 255,255,255" clearable>
            <template slot="prepend">rgb</template>
          </el-input>
        </div>
        <div class="button" @click="changeThree">
          <el-button>转换</el-button>
        </div>
      </div>
      <div class="main-two" v-clipboard:copy="allcolor" v-clipboard:success="onCopy" v-clipboard:error="onError">
        <div class="two-one">{{allcolor}}</div>
        <div class="two-two" :style="{background:allcolor}"></div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  components: {},
  data() {
    return {
      inputOne: "",
      rgbcolor: "",
      rgb: "",
      r: "",
      g: "",
      b: "",
      hex: "",
      all: "",
      allcolor: ""
    };
  },
  computed: {},
  watch: {},
  methods: {
    changeOne: function() {
      if (this.inputOne.length == 3 || this.inputOne.length == 6) {
        this.rgbcolor = this.toRgb(this.inputOne);
      } else {
        this.$message.error("十六进制代码长度为3位或6位");
      }
    },
    changeTwo: function() {
      if (this.r !== "" && this.g !== "" && this.b !== "") {
        var a = `rgb(${this.r},${this.g},${this.b})`;
        this.hex = this.toHex(a);
      } else {
        this.$message.error("请检查输入的内容是否为空");
      }
    },
    changeThree: function() {
      if (this.all !== "") {
        var a = `rgb(${this.all})`;
        this.allcolor = this.toHex(a);
      } else {
        this.$message.error("请检查输入的内容是否为空");
      }
    },
    copy: function(e) {
     
    },
    toRgb: function(hex) {
      var rgb = [];
      if (hex.length === 3) {
        // 处理 "#abc" 成 "#aabbcc"
        hex = hex.replace(/(.)/g, "$1$1");
      }
      hex.replace(/../g, function(color) {
        rgb.push(parseInt(color, 0x10)); //按16进制将字符串转换为数字
      });
      if (rgb.includes(NaN)) {
        this.$message.error("请输入正确的十六进制");
        return;
      } else {
        this.rgb = rgb.join(",");
      }
      return "rgb(" + rgb.join(",") + ")";
    },
    toHex: function(rgb) {
      // rgb(x, y, z)
      var color = rgb.toString().match(/\d+/g); // 把 x,y,z 推送到 color 数组里
      var hex = "#";
      for (var i = 0; i < 3; i++) {
        // 'Number.toString(16)' 是JS默认能实现转换成16进制数的方法.
        // 'color[i]' 是数组，要转换成字符串.
        // 如果结果是一位数，就在前面补零。例如： A变成0A
        hex += ("0" + Number(color[i]).toString(16)).slice(-2);
      }
      return hex;
    },
    onCopy: function (e) {
     this.$message({
          message: '复制成功',
          type: 'success'
        });
    },
    onError: function (e) {
      // this.$message({
      //     message: '复制成功',
      //     type: 'success'
      //   });
      
    }
  },
  //生命周期 - 创建完成（可以访问当前this实例）
  created() {},
  //生命周期 - 挂载完成（可以访问DOM元素）
  mounted() {
  },
  beforeCreate() {}, //生命周期 - 创建之前
  beforeMount() {}, //生命周期 - 挂载之前
  beforeUpdate() {}, //生命周期 - 更新之前
  updated() {}, //生命周期 - 更新之后
  beforeDestroy() {}, //生命周期 - 销毁之前
  destroyed() {}, //生命周期 - 销毁完成
  activated() {} //如果页面有keep-alive缓存功能，这个函数会触发
};
</script>
